import React, { useEffect, useState } from 'react';
import { graphql, StaticQuery } from 'gatsby';
import { useInView } from 'react-intersection-observer';

import Layout from '../components/layout';
import SEO from '../components/seo';
import PostCard from '../components/postCard';
import SideBar from '../components/SideBar';

import Banner from '../components/banner';

// import "../utils/global.scss"
import '../utils/normalize.css';
import '../utils/css/screen.css';
import '../utils/css/index.scss';

const BlogIndex = ({ data, location }) => {
    const [page, setPage] = useState(1);
    const [loading, setLoading] = useState(false);
    const siteTitle = data.site.siteMetadata.title;
    const facebook = data.site.siteMetadata.social.facebook;

    const posts = data.allMarkdownRemark.edges;
    let postCounter = 0;

    const { ref, inView } = useInView({
        /* Optional options */
        threshold: 0
    });
    useEffect(() => {
        if (!loading && inView && posts.length > page * 10) {
            setLoading(true);
            setTimeout(() => {
                setPage(prevPage => prevPage + 1);
                setLoading(false);
                // API or Your Functions to get data
            }, 500);
        }
    }, [inView]);
    return (
        <Layout location={location} title={siteTitle} facebook={facebook}>
            <SEO title="首頁" keywords={[`blog`, `gatsby`, `javascript`, `react`]} />
            {/* <Bio /> */}
            <Banner imgFluid={data.file.childImageSharp.fluid} />
            <div className="index-container">
                <SideBar />
                <div className="post-feed">
                    {posts.map(({ node }, index) => {
                        postCounter++;
                        // if (index > page * 10) {
                        //     return null;
                        // }
                        return (
                            <PostCard
                                key={node.fields.slug}
                                count={postCounter}
                                node={node}
                                postClass={`post`}
                            />
                        );
                    })}
                    {/* {loading && (
                        <div className="loading-container">
                            <div className="loader"></div>
                        </div>
                    )} */}
                </div>
            </div>
            <div ref={ref}></div>
        </Layout>
    );
};

const indexQuery = graphql`
    query {
        site {
            siteMetadata {
                title
                description
                social {
                    facebook
                }
            }
        }
        file(relativePath: { eq: "banner.png" }) {
            id
            childImageSharp {
                fluid(maxWidth: 295) {
                    ...GatsbyImageSharpFluid_noBase64
                }
            }
        }
        allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
            edges {
                node {
                    excerpt
                    fields {
                        slug
                    }
                    frontmatter {
                        date(formatString: "MMMM DD, YYYY")
                        title
                        description
                        categories
                        thumbnail {
                            childImageSharp {
                                fluid(maxWidth: 450) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                    timeToRead
                }
            }
        }
    }
`;

export default props => (
    <StaticQuery
        query={indexQuery}
        render={data => <BlogIndex location={props.location} props data={data} {...props} />}
    />
);
