import React from 'react';
import { Link } from 'gatsby';
import Img from 'gatsby-image';

export default props => {
    return (
        <article
            className={`post-card post-card-large ${props.postClass} ${
                props.node.frontmatter.thumbnail ? `with-image` : `no-image`
            }`}
        >
            <div className="post-card-link">
                <div className="post-card-content-container">
                    <div className="post-card-content">
                        <Link to={`/${props.node.fields.slug.split('-')[2]}`} className="post-link">
                            <h2 className="post-card-title">
                                {props.node.frontmatter.title || props.node.fields.slug}
                            </h2>
                        </Link>
                    </div>
                    <div>
                        <Link to={`/${props.node.fields.slug.split('-')[2]}`} className="post-link">
                            <p className="post-card-description">
                                {props.node.frontmatter.description}
                            </p>
                        </Link>
                    </div>
                    <div className="post-card-tag-container">
                        <p className="post-card-date">{props.node.frontmatter.date}</p>
                        <span className="dot">·</span>
                        <p className="post-card-read">{props.node.timeToRead}min</p>
                        <span className="dot">·</span>
                        <Link
                            className="post-card-cate-link"
                            to={`/categories/${props.node.frontmatter.categories}`}
                        >
                            <p className="post-card-cate">{props.node.frontmatter.categories}</p>
                        </Link>
                    </div>
                </div>
                <div className="post-image-container">
                    {props.node.frontmatter.thumbnail && (
                        <Img
                            className="post-bg-image"
                            fluid={props.node.frontmatter.thumbnail.childImageSharp.fluid}
                            alt={props.node.frontmatter.title}
                        />
                        // <div
                        //     className="post-bg-image"
                        //     style={{
                        //         backgroundImage: `url(${props.node.frontmatter.thumbnail.childImageSharp.fluid.src})`
                        //     }}
                        // ></div>
                    )}
                </div>
            </div>
        </article>
    );
};
