import React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import { useInView } from 'react-intersection-observer';

import Img from 'gatsby-image';

function SideBar() {
    const {
        file: {
            childImageSharp: { fixed }
        },
        tagsGroup,
        categoriesGroup
    } = useStaticQuery(
        graphql`
            query {
                tagsGroup: allMarkdownRemark(limit: 2000) {
                    group(field: frontmatter___tags) {
                        fieldValue
                    }
                }
                categoriesGroup: allMarkdownRemark(limit: 2000) {
                    group(field: frontmatter___categories) {
                        fieldValue
                    }
                }
                file(relativePath: { eq: "hippostick.png" }) {
                    id
                    childImageSharp {
                        fixed(width: 150, height: 150) {
                            ...GatsbyImageSharpFixed
                        }
                    }
                }
            }
        `
    );
    const { ref, inView } = useInView({
        /* Optional options */
        threshold: 0.1
    });
    const { ref: refs, inView: inViews } = useInView({
        /* Optional options */
        threshold: 0.1
    });
    return (
        <div className="mobile-hidden">
            <div className={!inView && inViews ? `fixed-spcae` : ''} ref={ref}></div>
            <div ref={refs} className={`side-bar ${!inView && inViews ? `fixed-side` : ''}`}>
                <div className="line-side">
                    <p className="header">贊助創作</p>
                    <div>
                        <a
                            target="_blank"
                            href="https://store.line.me/stickershop/author/1019955/zh-Hant"
                            targer="_blank"
                            alt="小河馬與牠的快樂夥伴"
                        >
                            <Img className="index-img" fixed={fixed} alt="hippo line stick" />
                            <p>Line 貼圖</p>
                        </a>
                    </div>
                </div>
                <div className="categories-side">
                    <p className="header">Category</p>
                    <div>
                        {categoriesGroup.group.map(({ fieldValue }) => (
                            <Link key={fieldValue} to={`/categories/${fieldValue}`}>
                                {fieldValue}
                            </Link>
                        ))}
                    </div>
                </div>
                <div className="tags-side">
                    <p className="header">Tag</p>
                    <div>
                        {tagsGroup.group.map(({ fieldValue }, index) => (
                            <div className="tag-item" key={fieldValue}>
                                <Link to={`/tags/${fieldValue.replace(/\./g, '_')}`}>
                                    {fieldValue}
                                </Link>
                                {index < tagsGroup.group.length - 1 && (
                                    <span className="dot">·</span>
                                )}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}
//hippostick.png
export default SideBar;
